import React from 'react';
import '../../App.css'

const Navigation = () => {
	return (
		<nav style={{ zIndex:'10', top:'0', paddingTop:'1rem', width:'100%', display:'flex', justifyContent:'space-between', fontSize:'2vh'}} 
		className='fixed bg-white pv2' >
			<a 
			style={{ fontFamily: 'GilroyExtraBold'}}
			href='www.google.com'
			className='ml4 dib no-underline black b'>ROMAN</a>
			<span 
			className='mr4' 
			style={{ fontFamily:'Libre Baskerville'}}>
				<a className='.dib no-underline black ph1' href='www.google.com'>en.</a>
				<a className='.dib no-underline black ph1' href='www.google.com'>spa.</a>
			</span>
		</nav>
	);
}

export default Navigation;